.ProgressBar {
    &-Title {
        text-align: start;
        font-weight: bold;
        margin-bottom: 0.5rem;
    }
    
    &-Bar {
        background-color: #dadada;
        border-radius: 0.3rem;
    }

    &-Progress {
        height: inherit;
        background-color: #035397;
        border-radius: inherit;
    }

    &-Level {
        color: white;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        text-transform: uppercase;
        font-size: 0.8rem;
    }
}